@-webkit-keyframes fadeInSmallText {
    from {opacity: 0;}
    to {opacity: 1;}
}

@keyframes fadeInSmallText {
    from {opacity: 0;}
    to {opacity: 1;}
}

@-webkit-keyframes fadeInBigText {
    from {opacity: 0;}
    to {opacity: 1;}
}

@keyframes fadeInBigText {
    from {opacity: 0;}
    to {opacity: 1;}
}

@-webkit-keyframes beat {
    from {-webkit-transform: scale(0.5);transform: scale(0.5);}
    to {-webkit-transform: scale(0.6);transform: scale(0.6);}
}

@keyframes beat {
    from {-webkit-transform: scale(0.5);transform: scale(0.5);}
    to {-webkit-transform: scale(0.6);transform: scale(0.6);}
}

h1 {
    font-size:4em;
    top:4em;
}
.center {
    width:100%;
    text-align: center;
    margin: 0 auto;
    margin-top:25vh;
    font-family: 'Montserrat', sans-serif;
    color:#6d6e72;
    position: absolute;
    z-index: 1;
}

.smallTitleFadeIn {
    opacity: 0;
    -webkit-animation-name: fadeInSmallText;
            animation-name: fadeInSmallText;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
}

#bigTitleFadeIn {
    opacity: 0;
    -webkit-animation-name: fadeInBigText;
            animation-name: fadeInBigText;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}


/*Taken from CSS-Tricks: https://css-tricks.com/examples/ShapesOfCSS*/
#heart {
    margin: auto;
    position: relative;
    width: 100px;
    height: 90px;
    -webkit-animation-name: beat;
            animation-name: beat;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-transform: scale(0.5);
        -ms-transform: scale(0.5);
            transform: scale(0.5);
}

#heart:before,
#heart:after {
    position: absolute;
    content: "";
    left: 50px;
    top: 0;
    width: 50px;
    height: 80px;
    background: red;
    border-radius: 50px 50px 0 0;
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
    -webkit-transform-origin: 0 100%;
        -ms-transform-origin: 0 100%;
            transform-origin: 0 100%;
}
#heart:after {
    left: 0;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-transform-origin: 100% 100%;
        -ms-transform-origin: 100% 100%;
            transform-origin :100% 100%;
}


