@-webkit-keyframes bounce {
    0% {
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    } 
    25% {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
    50% {
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
    }

    75% {
        -webkit-transform: translateY(-5px);
                transform: translateY(-5px);
    }
}

@keyframes bounce {
    0% {
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    } 
    25% {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
    50% {
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
    }

    75% {
        -webkit-transform: translateY(-5px);
                transform: translateY(-5px);
    }
}

/*Overall div containing the circles*/
#showCircles {
    position: absolute;
    margin-top: 5%;
    width: 100%;
    z-index: 1;
}

/*Control the flow of the circles within the div with Flexbox*/
.flex {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    padding-top: 5%;
    font-family: 'Open Sans Condensed', sans-serif;
    font-size: 20px;
    color: white;
}

/*Style for each individual circle; shape taken from CSS Tricks*/
.circle {
    width: 150px;
	height: 150px;
	background: red;
 border-radius: 75px;
    text-align: center;
}

/*Center the label in the circle*/
.centerCircleLabel {
    margin-top: 65px;
}

/*Circle bounce animation*/
.bounceAnimation {
    -webkit-animation-name: bounce;
            animation-name: bounce;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-fill-mode: none;
            animation-fill-mode: none;
}

/*---------------EDUCATION---------------*/
#Education {
    margin-top: 10%;
    padding: 2% 10% 2% 10%;
}

.captionFix {
    float: left;
    margin-right: 4%;
}


